import axios from "axios"
import Vue from "vue"
export default {
    create(data) {
        const token = Vue.$cookies.get('admin-token')
        return axios({
            method: "POST",
            url: '/nodes/admin',
            headers: {
                Authorization: `Bearer ${token}`

            },
            data
        })
    },
    getAll() {
        const token = Vue.$cookies.get('admin-token')
        return axios({
            method: "GET",
            url: '/nodes/admin',
            headers: {
                Authorization: `Bearer ${token}`
            },
            // params: {
            //     limit: 5,
            //     skip: 5,
            // }

        })
    },
    getOne(id) {
        const token = Vue.$cookies.get('admin-token')
        return axios({
            method: "GET",
            url: '/nodes/admin' + `/${id}`,
            headers: {
                Authorization: `Bearer ${token}`

            },

        })
    },
    edit(id, data) {
        const token = Vue.$cookies.get('admin-token')
        return axios({
            method: "PATCH",
            url: '/nodes/admin' + `/${id}`,
            headers: {
                Authorization: `Bearer ${token}`

            },
            data
        })
    },
    delete(id) {
        const token = Vue.$cookies.get('admin-token')
        return axios({
            method: "DELETE",
            url: '/nodes/admin' + `/${id}`,
            headers: {
                Authorization: `Bearer ${token}`

            },

        })
    },
}